:root {
  --bg: rgb(32, 24, 24);
  --pFont: #ffd00c;
  --cardBg: #000000;
  --sFont: #000;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

body {
  background: var(--bg);
  color: var(--sFont);
  height: 100vh;
  display: grid;
  place-content: center;
  text-align: center;
}


